import axios from "../../axios/axios-common";

const CourseStore = {
  namespaced: true,
  state: {
    courseList: [],
    courseInfo: []
  },
  getters: {
    getCourseList(state) {
      //  
      return state.courseList;
    },
    getCourseInfo(state) {
      //  
      return state.courseInfo;
    }
  },
  mutations: {
    setCourseList(state, payload) {
      state.courseList = payload;
    },
    setCourseInfo(state, payload) {
      state.courseInfo = payload;
    }
  },
  actions: {
    //   
    reqCourseList(context, user_id) {
      return axios
        .get("/courselist/" + user_id)
        .then(response => {
          if (response.data.message == "course list") {
            context.commit("setCourseList", response.data.result);
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    //  
    reqCourseInfo(context, fd) {
      return axios
        .get("/course/" + fd.user_id + "?course_id=" + fd.course_id)
        .then(response => {
          console.log(response.data);
          if (response.data.message == "course list info") {
            context.commit("setCourseInfo", response.data.result); //   store .
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    //  
    reqCreateCourse(context, id) {
      return axios
        .post("/course/" + id)
        .then(response => {
          console.log(response.data);
          if (response.data.message == "success") {
            return {
              result: response.data.result,
              msg: "  .",
              color: "success"
            };
          } else {
            return {
              result: false,
              msg: "   .",
              color: "success"
            };
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 
    reqCreate(context, list) {
      console.log(list);
      return axios
        .post("/course/add", list)
        .then(response => {
          console.log(response.data);
          if (response.data.message == "insert course") {
            context.commit("setCourseInfo", list);
            return {
              result: response.data.result,
              msg: true,
              color: "success"
            };
          } else {
            return {
              result: false,
              msg: "  .",
              color: "success"
            };
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 
    reqDeleteCourse(context, course_id) {
      return axios
        .delete("/course/" + course_id)
        .then(response => {
          if (response.message == "success") {
            return true;
          } else return false;
        })
        .catch(error => {
          console.log(error);
          return false;
        });
    }
  }
};

export default CourseStore;
